.overlay-exit-intervention {
  visibility: visible;
  z-index: 4;
  opacity: 1;

  .overlay-wrap {
    transform: scale(1);
    max-width: 960px;

    .cancel {
      display: none;
    }

    .overlay-content {
      // width: 100%;
    }

    .overlay-content-container {
      display: flex;
      padding: 0;
      min-height: 54vh;
      overflow: hidden;
      background-color: #fff8f8;

      .overlay-image-container {
        padding: 0;
        max-width: 56%;
        overflow: hidden;
      }

      .overlay-text-container {
        width: 44%;
        // background-color: #fff8f8;
        text-align: center;
        margin: auto;

        p {
          color: #000;
        }

        h1 {
          font-size: 32px;
          font-weight: 500;
          margin-bottom: 8px;
        }

        .cta {
          letter-spacing: 0;
          width: 185px;
        }

        .overlay-cta-container {
          width: 100%;
          padding-top: 36px;
          padding-bottom: 16px;
        }

        .dismiss {
          border-bottom: 1px solid #9e9e9e;
          color: #9e9e9e;
          text-decoration: none;
        }
      }
    }
  }
}